import React, { Component } from 'react'

class Venue extends Component {
  render () {
    return (
      <div>
        <h2>Stasetning</h2>
        <p>Athfnin verur haldin  Frkirkju Reykjavkur ann 15. sept kl 16:00. Veislan verur svo 17:30  sal Feraflags slands, Mrkinni 6.</p>

        <p>Duis a turpis sed lacus dapibus elementum sed eu lectus.</p>
      </div>
    )
  }
}

export default Venue



